.wrapper {
  max-width: 500px;
  margin: 0 auto;
}
.textarea {
  width: 100%;
  height: 150px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  resize: none;
}
.input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}
.btn {
  padding: 10px 20px;
  border-radius: 30px;
  border: none;
}
.btn:hover,
.btn:focus {
  background-color: darkgray;
  cursor: pointer;
}
